body {
  opacity: 0;
  transition: all 1s ease;
}

body.loaded {
  opacity: 1;
  overflow: visible;
  overflow-x: hidden;
}

body.loaded .loader {
  opacity: 1;
}

body.loaded .loader.loaded {
  opacity: 0;
}

body .build-info {
  left: 0;
}

body .loader {
  display: table;
  position: absolute;
  background: #fff;
  height: 100vh;
  width: 100vw;
  transition: all 0.2s ease-out;
  opacity: 1;
  z-index: 9999;
}

body .loader .bounce {
  display: table-cell;
  vertical-align: middle;
  height: 100vh;
  position: relative;
  top: 20%;
}

body .loader svg {
  animation: downbouncespin 4s forwards infinite;
  display: table-cell;
  height: 95px;
  width: 95px;
  text-align: center;
  vertical-align: middle;
  margin: 0 auto;
  position: absolute;
  top: 25%;
  left: 0;
  right: 0;
}

body #jmpress {
  transform: translate(0%, 0%) scaleX(1) scaleY(1) scaleZ(1) !important;
  opacity: 1;
  transition: opacity 1s ease-out;
}

body .step {
  transition: all 0.5s ease-in-out;
  will-change: auto;
  transform: translate(-50%, 200%) translate3d(0px, 0px, 0px) rotateX(0deg) rotateY(0deg) rotateZ(0deg) scaleX(1) scaleY(1) scaleZ(1) !important;
}

body .step.active {
  transform: translate(-50%, -50%) translate3d(0px, 0px, 0px) rotateX(0deg) rotateY(0deg) rotateZ(0deg) scaleX(1) scaleY(1) scaleZ(1) !important;
}

body .step.passed {
  transform: translate(-50%, -500%) translate3d(0px, 0px, 0px) rotateX(0deg) rotateY(0deg) rotateZ(0deg) scaleX(1) scaleY(1) scaleZ(1) !important;
}

.path {
  opacity: 0;
}

.path-animate {
  opacity: 1;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: dash 10s linear alternate;
  animation-fill-mode: forwards;
}

.Explorer .path-animate {
  stroke-dasharray: none;
  stroke-dashoffset: none;
}

@keyframes dash {
  from {
    stroke-dashoffset: 1000;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.hideme {
  opacity: 0;
}

.fade-left,
.fade-right,
.fade-up {
  opacity: 0;
  transition: all 1s ease;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  transform-style: preserve-3d;
  will-change: opacity;
}

.fade-left *,
.fade-right *,
.fade-up * {
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  transform-style: preserve-3d;
  will-change: opacity;
}

@media screen and (max-width: 768px) {
  .fade-left,
  .fade-right,
  .fade-up {
    opacity: 1;
    transition: none;
    visibility: visible;
  }
}

.fade-left.show,
.fade-right.show,
.fade-up.show {
  transform: translate3d(0, 0, 0);
  will-change: auto;
  opacity: 1;
}

.fade-left {
  transform: translate3d(-45px, 0, 0);
}

@media screen and (max-width: 768px) {
  .fade-left {
    transform: none;
  }
}

.fade-right {
  transform: translate3d(45px, 0, 0);
}

@media screen and (max-width: 768px) {
  .fade-right {
    transform: none;
  }
}

.fade-up {
  transform: translate3d(0, 0, -45px);
}

@media screen and (max-width: 768px) {
  .fade-up {
    transform: none;
  }
}

.cut-up {
  will-change: auto;
  opacity: 0;
}

.cut-up.animate {
  animation-delay: 0.8s;
  animation-name: cutTextDown;
  animation-duration: 0.5s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}

.cut-left {
  will-change: auto;
  opacity: 0;
}

.cut-left.animate {
  animation-delay: 0.1s;
  animation-name: cutLeft;
  animation-duration: 0.5s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}

